"use client";
import { mediaLoader } from "@/lib/media-loader";
import { useGlobalStore } from "@/lib/store";
import Image from "next/image";
import React, { useEffect, useRef, useState } from "react";

function QuizAnimation() {
  const { theme } = useGlobalStore();

  const containerRef = useRef<HTMLDivElement>(null);
  const [isVisible, setIsVisible] = useState(false);
  const isTeal = theme == "teal";

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true);
        }
      },
      { threshold: 0.1 },
    );

    const element = containerRef.current;
    if (element) {
      observer.observe(element);
    }

    return () => {
      if (element) {
        observer.unobserve(element);
      }
    };
  }, []);
  return (
    <>
      <div
        ref={containerRef}
        className={`relative flex w-full flex-col-reverse sm:flex-row justify-center gap-20 sm:gap-40 pt-8 sm:pt-16
        `}
      >
        <div className="container mx-auto flex w-full flex-col-reverse sm:flex-row justify-center gap-0 sm:gap-40 px-6 sm:px-32 ">
          <div
            className={`relative w-full transition-all duration-1000 ease-out  ${
              isVisible
                ? "opacity-100 translate-x-0"
                : "opacity-0 -translate-x-full"
            }`}
          >
            <div
              className={`flex flex-col sm:flex-row relative sm:transform ${isTeal ? "" : "sm:justify-center"}`}
            >
              {/* <div className="w-fit h-auto self-center sm:self-auto"> */}
              <Image
                src={mediaLoader(
                  isTeal
                    ? "/products-page/gut-gif.v2.gif"
                    : "/products-page/ww-animation.png",
                )}
                alt="gut animation"
                width={270}
                height={270}
                className={`pointer-events-none ${isTeal ? "" : "sm:w-75 "}`}
                unoptimized
              />
              {/* </div> */}
              {isTeal && (
                <div className=" sm:self-end relative w-fit">
                  <span className="font-light text-xl sm:text-3xl text-black sm:absolute sm:-top-10  sm:right-0">
                    Only
                  </span>

                  <h2 className="text-primary w-fit font-bold text-5xl sm:text-7xl">
                    2%
                  </h2>
                </div>
              )}
            </div>

            {isTeal && (
              <>
                <p className="font-light text-base sm:text-2xl text-black my-2 sm:mb-4">
                  people have a strong, <br className="sm:hidden" />
                  balanced gut.
                </p>

                <p className="text-[8px] sm:text-[10px] font-light">
                  *As per the gut pro quiz responses.
                </p>

                <Image
                  src="/products-page/bg-image.png"
                  alt="bg-image"
                  width={150}
                  height={200}
                  className="absolute top-0 sm:-left-4 -z-10"
                />
              </>
            )}
          </div>

          <div
            className={`sm:flex-1 sm:max-w-1/2 sm:self-start transition-all duration-1000 ease-out text-center sm:text-left
            ${
              isVisible
                ? "opacity-100 translate-y-0 sm:translate-x-0"
                : "opacity-0 -translate-y-20 sm:translate-x-full"
            }
            ${isTeal ? "" : "mb-8 sm:m-0"}
        `}
          >
            <h2
              className={`whitespace-nowrap text-foreground text-3xl sm:text-4xl sm:font-light `}
            >
              {isTeal ? "Have You Taken The" : "Decode Your"}
            </h2>

            <h2
              className={`whitespace-nowrap font-bold italic text-[40px] sm:text-5xl text-primary `}
            >
              {isTeal ? "GUT PRO QUIZ?" : "Calorie Code"}
            </h2>
          </div>
        </div>

      </div>

      <div
        className={`relative w-full aspect-[1.92] ${isTeal ? "sm:aspect-[4.54]" : "sm:aspect-[4.3]"} -mt-[13%] lg:-mt-[10%] outline-none border-x-primary`}
      >
        <Image
          src={`/products-page/${theme}-curved-svg-products.svg`}
          alt="curved-svg-products"
          width={1280}
          height={282}
          // className="hidden sm:block w-full"
          className="hidden sm:block w-full absolute inset-x-0 top-0 "
          sizes="100vw"
        />

        <Image
          src={`/products-page/${theme}-curved-svg-products-mobile.svg`}
          alt="curved-svg-products"
          width={375}
          height={195}
          // className="w-full sm:hidden  border-t border-white"
          className="w-full sm:hidden absolute inset-x-0 top-0 "
          sizes="100vw"
        />

        <div
          className={`absolute mt-6 sm:m-0 
            sm:px-32
            transition-all duration-1000 ease-out
            ${
              isVisible
                ? "opacity-100 translate-x-0"
                : "opacity-0 translate-x-full"
            }
             top-1/2 -translate-y-1/2 sm:-translate-y-1/4 right-2 sm:right-0
            `}
        >
          <h3
            className={`absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 -z-1 text-9xl sm:text-[180px]  font-black text-white/10`}
          >
            ?
          </h3>

          <h2
            className={`text-xs sm:text-lg text-center line-clamp-3 max-w-[150px] sm:max-w-xs text-white`}
          >
            {isTeal
              ? "Do you fall in the strong, moderate, or weak gut score range?"
              : "How many calories are coming from your cooking?"}
          </h2>
        </div>
      </div>
    </>
  );
}

export default QuizAnimation;
